import Cookie from "js-cookie";
import { Base64 } from "js-base64";

import { ValidationObserver, ValidationProvider } from "vee-validate";

export default {
  middleware: ["session-control", "admin"],
  layout: "admin",
  components: {
    ValidationObserver,
    ValidationProvider,
  },
  mounted() {
    this.retrieveData();
  },
  data() {
    return {
      counter:
        this.data !== null &&
        this.data !== undefined &&
        this.data !== "" &&
        this.data.values !== null &&
        this.data.values !== undefined &&
        this.data.values !== ""
          ? this.data.values.length
          : 0,
      e1: 1,
      imageData: [],
      data: {
        images: [],
      },
      searchTitle: null,
      headers: [
        { text: "#", align: "center", value: "rank" },
        { text: "Görsel", align: "center", value: "img_url", sortable: false },
        { text: "Kapak Fotoğrafı", align: "center", value: "isCover" },
        { text: "Durum", align: "center", value: "isActive" },
        {
          text: "İşlemler",
          align: "center",
          value: "actions",
          sortable: false,
        },
      ],
      page: 1,
      totalPages: 0,
      pageSize: 25,
      pageSizes: [25, 50, 100, 200, 500, 1000],
      loading: false,
      options: {
        url:
          process.env.apiBaseUrl +
          "panel/diseases/create-file/" +
          this.$route.params.id,
        headers: {
          Authorization:
            "Bearer " +
            (Cookie.get("userData") !== null &&
            Cookie.get("userData") !== undefined &&
            Cookie.get("userData") !== ""
              ? JSON.parse(Base64.decode(Cookie.get("userData"))).api_token
              : null),
        },
        params: {
          title: null,
        },
        uploadMultiple: true,
        parallelUploads: 10,
      },
      userData:
        Cookie.get("userData") !== null &&
        Cookie.get("userData") !== undefined &&
        Cookie.get("userData") !== ""
          ? JSON.parse(Base64.decode(Cookie.get("userData")))
          : null,
      tab: null,
      items: [{ tab: "Hastalık Değerleri" }],
    };
  },
  computed: {
    img_url() {
      return process.env.apiPublicUrl;
    },
  },
  validate({ params }) {
    return params.id !== null ? params.id : null;
  },
  async asyncData({ params, error, $axios }) {
    try {
      const { data } = await $axios.get(
        process.env.apiBaseUrl + "panel/diseases/update/" + params.id
      );
      if (
        data.data.values.length === 0 ||
        data.data.values.length === null ||
        data.data.values.length === undefined
      ) {
        data.data.values.push({
          title: "",
          min: "",
          max: "",
          type: "",
          id: 0,
        });
      }

      return data;
    } catch (e) {
      error({ message: "Hastalık Bilgisi Bulunamadı.", statusCode: 404 });
    }
  },
  methods: {
    selectCover() {
      this.e1 = 3;
      this.retrieveData();
    },
    getRequestParams(searchTitle, page, pageSize) {
      let params = {};
      params["title"] = searchTitle;
      params["page"] = page;
      params["size"] = pageSize;
      return params;
    },
    retrieveData(url) {
      let urlParam = "get-all";
      if (url !== undefined && url !== "" && url !== null) {
        urlParam = url;
      }
      const params = this.getRequestParams(
        this.searchTitle,
        this.page,
        this.pageSize
      );
      this.$axios
        .get(
          `${process.env.apiBaseUrl}panel/datatables/${urlParam}?table=diseases_file&page=${params.page}&per_page=${params.size}&search=${params.title}&search_columns=name,email,phone&where_column=diseases_id&where_value=${this.data._id.$oid}&joins=diseases_file`,
          {
            json: true,
            withCredentials: false,
            mode: "no-cors",
            headers: {
              "Access-Control-Allow-Origin": "*",
              "Access-Control-Allow-Headers":
                "Origin, Content-Type, X-Auth-Token, Authorization",
              "Access-Control-Allow-Methods":
                "GET, POST, PATCH, PUT, DELETE, OPTIONS",
              "Access-Control-Allow-Credentials": true,
              "Content-type": "application/json",
              Authorization: "Bearer " + this.userData.api_token,
            },
            credentials: "same-origin",
          }
        )
        .then((response) => {
          this.imageData = response.data.data.data.map(this.getDisplayData);
          this.totalPages = response.data.data.last_page;
        })
        .catch((err) => console.log(err))
        .finally(() => (this.loading = false));
    },
    handlePageChange(value) {
      this.page = value;
      this.retrieveData();
    },
    handlePageSizeChange(size) {
      this.pageSize = size;
      this.page = 1;
      this.retrieveData();
    },
    refreshList() {
      this.retrieveData();
    },
    deleteData(id) {
      this.$axios
        .delete(process.env.apiBaseUrl + "panel/diseases/delete/" + id, {
          json: true,
          withCredentials: false,
          mode: "no-cors",
          headers: {
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Headers":
              "Origin, Content-Type, X-Auth-Token, Authorization",
            "Access-Control-Allow-Methods":
              "GET, POST, PATCH, PUT, DELETE, OPTIONS",
            "Access-Control-Allow-Credentials": true,
            "Content-type": "application/json",
            Authorization: "Bearer " + this.userData.api_token,
          },
          credentials: "same-origin",
        })
        .then((response) => {
          if (response.data.success) {
            this.$izitoast.success({
              title: response.data.title,
              message: response.data.msg,
              position: "topCenter",
              displayMode: "once",
            });
            this.refreshList();
          } else {
            this.$izitoast.error({
              title: response.data.title,
              message: response.data.msg,
              position: "topCenter",
              displayMode: "once",
            });
          }
        });
    },
    isActiveSetter(id) {
      this.$axios
        .get(
          process.env.apiBaseUrl +
            "panel/datatables/is-active-setter?table=diseases_file&id=" +
            id,
          {
            json: true,
            withCredentials: false,
            mode: "no-cors",
            headers: {
              "Access-Control-Allow-Origin": "*",
              "Access-Control-Allow-Headers":
                "Origin, Content-Type, X-Auth-Token, Authorization",
              "Access-Control-Allow-Methods":
                "GET, POST, PATCH, PUT, DELETE, OPTIONS",
              "Access-Control-Allow-Credentials": true,
              "Content-type": "application/json",
              Authorization: "Bearer " + this.userData.api_token,
            },
            credentials: "same-origin",
          }
        )
        .then((response) => {
          if (response.data.success) {
            this.$izitoast.success({
              title: response.data.title,
              message: response.data.msg,
              position: "topCenter",
              displayMode: "once",
            });
            this.refreshList();
          } else {
            this.$izitoast.error({
              title: response.data.title,
              message: response.data.msg,
              position: "topCenter",
              displayMode: "once",
            });
          }
        });
    },
    isCoverSetter(id) {
      this.$axios
        .get(
          process.env.apiBaseUrl +
            "panel/datatables/is-cover-setter?table=diseases_file&foreign_column=diseases_id&id=" +
            id,
          {
            json: true,
            withCredentials: false,
            mode: "no-cors",
            headers: {
              "Access-Control-Allow-Origin": "*",
              "Access-Control-Allow-Headers":
                "Origin, Content-Type, X-Auth-Token, Authorization",
              "Access-Control-Allow-Methods":
                "GET, POST, PATCH, PUT, DELETE, OPTIONS",
              "Access-Control-Allow-Credentials": true,
              "Content-type": "application/json",
              Authorization: "Bearer " + this.userData.api_token,
            },
            credentials: "same-origin",
          }
        )
        .then((response) => {
          if (response.data.success) {
            this.$izitoast.success({
              title: response.data.title,
              message: response.data.msg,
              position: "topCenter",
              displayMode: "once",
            });
            this.refreshList();
          } else {
            this.$izitoast.error({
              title: response.data.title,
              message: response.data.msg,
              position: "topCenter",
              displayMode: "once",
            });
          }
        });
    },
    getDisplayData(data) {
      return {
        rank: data.rank,
        id: data._id.$oid,
        img_url: this.img_url + "public/storage/" + data.img_url,
        isCover: data.isCover,
        isActive: data.isActive,
      };
    },

    cloneProperty() {
      this.data.values.push({
        title: "",
        value: "",
        type: "",
        id: ++this.counter,
      });
    },
    removeProperty(id) {
      this.data.values.splice(id, 1);
    },
    cloneProperty2() {
      this.data.criteria_values.push({
        value: "",
        type: "",
        id: ++this.counter2,
      });
    },
    removeProperty2(id) {
      this.data.criteria_values.splice(id, 1);
    },
    onComplete(e) {
      if (JSON.parse(e.xhr.response).success) {
        this.$izitoast.success({
          title: JSON.parse(e.xhr.response).title,
          message: JSON.parse(e.xhr.response).msg,
          position: "topCenter",
          displayMode: "once",
        });
      } else {
        this.$izitoast.error({
          title: JSON.parse(e.xhr.response).title,
          message: JSON.parse(e.xhr.response).msg,
          position: "topCenter",
          displayMode: "once",
        });
      }
    },
    editDiseases() {
      let formData = new FormData(this.$refs.diseasesForm);
      this.$axios
        .post(
          process.env.apiBaseUrl +
            "panel/diseases/update/" +
            this.data._id.$oid,
          formData,
          {
            json: true,
            withCredentials: false,
            mode: "no-cors",
            headers: {
              "Access-Control-Allow-Origin": "*",
              "Access-Control-Allow-Headers":
                "Origin, Content-Type, X-Auth-Token, Authorization",
              "Access-Control-Allow-Methods":
                "GET, POST, PATCH, PUT, DELETE, OPTIONS",
              "Access-Control-Allow-Credentials": true,
              "Content-Type":
                "multipart/form-data; boundary=" + formData._boundary,
              Authorization: "Bearer " + this.userData.api_token,
            },
          }
        )
        .then((response) => {
          if (response.data.success) {
            this.$izitoast.success({
              title: response.data.title,
              message: response.data.msg,
              position: "topCenter",
            });
            setTimeout(() => {
              this.$router.go(decodeURIComponent("/panel/diseases"));
            }, 2000);
          } else {
            this.$izitoast.error({
              title: response.data.title,
              message: response.data.msg,
              position: "topCenter",
            });
          }
        });
    },
  },
};
